:root {
  --black-main: #14151f;
  --background-main: #f1f5f8;
  --teal-1: #38a89d;
  --teal-2: #4dc0b5;
  --teal-3: #64d5ca;
  --teal-4: #a0f0ed;
  --blue-1: #2779bd;
  --blue-2: #3490dc;
  --blue-3: #6cb2eb;
  --blue-4: #bcdefa;
  --indigo-1: #5661b3;
  --indigo-2: #6574cd;
  --indigo-3: #7886d7;
  --indigo-4: #b2b7ff;
  --pink-1: #eb5286;
  --pink-2: #f66d9b;
  --pink-3: #fa7ea8;
  --pink-4: #ffbbca;
}

* {
  padding: 0;
  margin: 0;
